.slider__block{background-size:cover;background-position:center;width:100%;height:100%;display:flex;flex-direction:column;justify-content:flex-end;transition:all 1s}.slider__title{text-transform:uppercase;color:white;font-weight:300;font-size:2.2em}.slider__bottom{width:90%;margin:20px auto;min-height:120px;position:relative}.slider__genre{font-size:1em;margin:15px 0;color:white}.slider__genre::first-letter{text-transform:uppercase}.slider__rating{width:200px;height:70px;background-color:rgba(73,76,98,0.2);border-radius:5px;border:solid 2px rgba(255,255,255,0.2);position:absolute;bottom:10px;color:white;right:40px;display:flex;flex-direction:column;justify-content:space-around;padding:7px 10px}.slider__rating_bottom{display:flex;flex-direction:row;justify-content:space-between}.slider__rating_number{font-size:19px;border:0.1px solid white;padding:7px}.slider__rating_stars{display:flex;width:70%;justify-content:space-between;margin-bottom:5px}.slider__rating_stars>span{font-size:25px;color:#ff0079}@media (max-width: 839px){.slider__block{justify-content:center;background-position:left}.slider__title{display:block;margin:0 auto;text-align:center;text-shadow:1px 1px 2px black}.slider__genre{display:block;text-align:center;font-size:1.1em;text-shadow:1px 1px 2px black}.slider__bottom{height:350px}.slider__rating{display:block;position:relative;top:0;right:0;margin:0 auto 25px;text-shadow:1px 1px 2px black;box-shadow:0 0 10px rgba(0,0,0,0.5)}}
