.footer__first{width:90%;margin:0 auto;padding:10px 0;display:flex;flex-direction:row;justify-content:space-between;align-items:center}.footer__second{width:90%;margin:0 auto;padding:10px 0;display:flex;flex-direction:row;justify-content:space-between;align-items:center;color:#80818a;font-size:0.7em}.footer__media-ul{display:flex;flex-direction:row;justify-content:flex-end;min-width:100px;margin:0;padding:0}.footer__media-ul li{width:30px;height:30px;margin-left:10px;list-style-type:none;transition:all 0.5s;background-size:100% 100%;cursor:pointer}.footer__media-ul .li_facebook{background-image:url(./assets/images/icon__facebook_grey.png)}.footer__media-ul .li_facebook:hover{background-image:url(./assets/images/icon__facebook.png)}.footer__media-ul .li_twitter{background-image:url(./assets/images/icon__twitter_grey.png)}.footer__media-ul .li_twitter:hover{background-image:url(./assets/images/icon__twitter.png)}.footer__media-ul .li_linkedin{background-image:url(./assets/images/icon__linkedin_grey.png)}.footer__media-ul .li_linkedin:hover{background-image:url(./assets/images/icon__linkedin.png)}.footer__media-ul .li_instagram{background-image:url(./assets/images/icon__insta_grey.png)}.footer__media-ul .li_instagram:hover{background-image:url(./assets/images/icon__insta.png)}.footer__nav-ul{display:flex;flex-direction:row;justify-content:flex-end;min-width:100px;margin:0;padding:0}.footer__nav-ul>li{list-style-type:none;color:#80818a;margin-left:10px;cursor:pointer}.footer__nav-ul>li:hover{color:#ff0079}
