.post__wrap{min-height:300px;width:350px;overflow:hidden;margin:25px 0;box-shadow:0 2px 35px 0 rgba(0,0,0,0.1);transition:all linear 0.3s;border-radius:3px}.post__wrap:hover{box-shadow:0 2px 35px 0 rgba(0,0,0,0.4);cursor:pointer}.post__text-wrap{margin:0;display:flex;flex-direction:row;padding:10px 12px;justify-content:space-between;background-color:#ffffff}.post__text-block{width:75%;display:flex;flex-direction:column;justify-content:space-around}.post__title{text-overflow:ellipsis;font-size:1.6em;font-weight:450;white-space:nowrap;overflow:hidden}.post__title::first-letter{text-transform:uppercase}.post__genre{text-overflow:ellipsis;font-size:1.3em;color:#babbc3;white-space:nowrap;overflow:hidden}.post__genre::first-letter{text-transform:uppercase}.post__imgBlock{display:block;position:relative}.post__imgBlock img{width:100%}.post__rating{color:#ff0079;border-radius:5px;border:solid 2px #ff0079;width:40px;height:35px;align-self:center;display:flex;justify-content:center;align-items:center;font-weight:bold}.post__year{position:absolute;left:20px;bottom:20px;color:#babbc3;font-size:1.5em}
